	const handleLoadClientsFromAPI = async () => {
		let route = window.location.href.substring(window.location.href.lastIndexOf('/') + 1)
		let filter: any = []
		if (route === 'open') {
			filter.push({
				column: 'status',
				value: 'open'
			})
			setFilter(filter)
		} else if (route === 'unassigned') {
			filter.push({
				column: 'status',
				value: 'unassigned'
			})
			setFilter(filter)
		} else {
			route = ''
		}

		let user: string;
		let isUndStaff: boolean = false
		let storage = await Utils.StorageService('user_data', 'username')
		user = storage.result;
		let group = await Utils.StorageService('user_data', 'group')
		if (group.result['name'].includes('UNDERWRITING_STAFF')) {
			isUndStaff = true	
		}
		setRoute(route);
		setUserName(user);
		setUserStaff(isUndStaff);
		setLoading(true);

		API.getTickets(limit, limit * page, query, sort, queryCount, filter, user, isUndStaff, route).then(response => {
			if (response && response.error === undefined) {
				console.log('ticket response', response)
				const tmpticketlist: any[] = [];
				response.data.forEach(item => {
					tmpticketlist.push({
						ticket_id: item.app_number,
						status: item.status,
						ticket_type: item.ticket_type ? item.ticket_type : '-',
						client: item.client_name ? item.client_name : '-',
						updated_on: new Date(item.date_updated),
						ticket_owner: item.created_by
							&& item.created_by.first_name
							&& item.created_by.middle_name
							&& item.created_by.last_name
							? `${item.created_by.first_name} ${item.created_by.middle_name} ${item.created_by.last_name}`
							: '-'
					})
				})
				setTicketList(tmpticketlist)
				setTotalCount(response.count.count);
				setLoading(false);
			} else {
				setRows(response.data);
				setTotalCount(0);
				setLoading(false);
				console.log('getbenefitlist api error: ', response);
			}
		})
			.catch((error) => {
				setLoading(false);
				setRows([]);
				setTotalCount(0);
				console.log('error in API Call', error)
			})
		API.getTicketCounts(query, queryCount, filter, user, isUndStaff, route)
			.then((response) => {
				console.log('count response', response)
				setStatusCounts(response)
			})
			.catch((error) => {
				console.log('get Count error', error)
			})
	}

