	const renderTicketListRow = (columns: any[], row: any, index: number): JSX.Element => {
		let allowRedirectClick: boolean = row && row.status && ['OPEN'].includes(row.status)

		const style: React.CSSProperties = {
			backgroundColor: (index % 2 === 0 ? '#FFFFFF' : '#F6F7FA'),
			cursor: (allowRedirectClick ? 'pointer' : undefined),
		};

		return (
			<TableRow style={style}>
				{columns.map((item) => {
					if (item.filterType === 'date') {
						return (<TableCell>{
							moment(row[item.key]).isValid() ?
								GlobalFunction.toCommonDateString(row[item.key]) :
								'---'
						}</TableCell>)
					} else {
						return (<TableCell>{row[item.key]}</TableCell>)
					}
				})}
			</TableRow>
		);
	}


	const handleOnChangeFilter = (columnName: string, value: any) => {
		if (typingTimeout) {
			clearTimeout(typingTimeout);
		}
		const chkindex = whereFilter.findIndex(item => item.column === columnName)
		if (chkindex !== -1) {
			whereFilter[chkindex]['value'] = value
		} else {
			whereFilter.push({
				column: columnName,
				value: value
			})
		}

		setTypingTimeout(setTimeout(function () {
			setLoading(true);
			filter.forEach(item => {
				const chk = whereFilter.findIndex(item2 => item2.column === item.column)
				// console.log('check filter',chk,item,whereFilter)
				if (chk === -1) {
					whereFilter.push(item)
				}
			})
			let tmpWhere: any[] = []
			whereFilter.forEach((item) => {
				if (item.value.trim() !== '') {
					tmpWhere.push(item)
				}
			})
			// console.log('tmpWhere',tmpWhere,whereFilter,filter)
			setPage(0)
			setFilter(tmpWhere)
			API.getTickets(limit, limit * 0, query, sort, queryCount, tmpWhere, userName, userStaff, route)
				.then(response => {
					if (response && response.error === undefined) {
						console.log('ticket response', response)
						const tmpticketlist: any[] = [];
						response.data.forEach(item => {
							tmpticketlist.push({
								ticket_id: item.app_number,
								status: item.status,
								ticket_type: item.ticket_type ? item.ticket_type : '-',
								client: item.client_name ? item.client_name : '-',
								updated_on: new Date(item.date_updated),
								ticket_owner: item.created_by
									&& item.created_by.first_name
									&& item.created_by.middle_name
									&& item.created_by.last_name
									? `${item.created_by.first_name} ${item.created_by.middle_name} ${item.created_by.last_name}`
									: '-'
							})
						})
						setTicketList(tmpticketlist)
						setTotalCount(response.count.count);
						setLoading(false);
						// setRows([]);
						console.log('error in API Call', response.error)
					}
				})
			API.getTicketCounts(query, queryCount, whereFilter, userName, userStaff, route)
				.then((response) => {
					console.log('count response', response)
					setStatusCounts(response)
				})
				.catch((error) => {
					console.log('get Count error', error)
				})
		}, 3000)
		);
	}

